<script>
	import { createEventDispatcher } from 'svelte';
	
	const dispatch = createEventDispatcher();
	
	export let options;
	export let selected;
	export let placeholder;
	export let search = false;
	
	let expanded = false;
	let filter = '';
	let active = null;
	let filtered;
	let el;
	let input;
	
	$: regex = filter ? new RegExp(filter, 'i') : null;
	$: {
		filtered = regex ? options.filter(option => regex.test(option.name)) : options;
		active = 0;
	}
	
	function toggle(ev) {
		ev.stopPropagation();
		filter = '';
		expanded = !expanded;
	}
	
	$: if (input && expanded) {
		input.focus();
	}
	
	function select(option) {
		dispatch('select', {
			code: option.code
		});
		selected = option;
		expanded = false;
	}
	
	function unSelect(ev) {
		ev.stopPropagation();
		selected = null;
		dispatch('select', {
			code: null
		});
	}

	function doKeyup(ev) {
		if (filtered[0] && Number.isInteger(active)) {
			if (ev.keyCode === 13) {
				select(filtered[active]);
			} else if (ev.keyCode === 38) {
				active -= active > 0 ? 1 : 0;
			} else if (ev.keyCode === 40) {
				active += active < filtered.length - 1 ? 1 : 0;
			}
		}
	}
	
	$: document.onclick = function(e){
		if(e.target !== el){
			expanded = false;
		}
	};
</script>

<style>
	a {
		text-decoration: none;
		display: block;
		padding: 0;
		border: 2px solid #206095 !important;
	}
	a span {
		display: inline-block;
		padding: 10px 5px;
	}
	.selection {
		overflow: hidden;
		max-width: calc(100% - 50px);
    white-space: nowrap;
	}
	#dropdown ul {
		list-style: none;
		background: #eee;
		margin: 0;
		padding: 0;
		max-height: 200px;
		overflow-y: auto;
		overflow-x: hidden;
	}
	#dropdown li {
		line-height: 1em;
		padding: 6px 0;
	}
	#dropdown .highlight {
		color: #fff;
		background-color: #206095;
		font-weight: 500;
	}
	/* normalize the input elements, make them look like everything else */
	#select input {
		width: calc(100% - 40px);
		box-sizing: border-box;
		background: transparent;
		font-family: inherit;
		font-size: inherit;
		color: inherit;
		font-weight: inherit;
		line-height: inherit;
		display: inline-block;
		padding: 10px 5px;
		margin: 0;
		background-color: #fff;
		border: 2px solid #206095 !important;
		border-radius: 0px;
		-webkit-appearance: none;
		-moz-appearance: none;
	}
	#select input:focus {
		outline: none;
	}
	/* custom field (drop-down, text element) styling  */
	#select {
		display: block;
		width: 100%;
		position: relative;
	}
	.active {
		z-index: 1000;
	}
	/* the toggle is the visible part in the form */
	#toggle,
	#select input {
		line-height: inherit;
		color: #206095;
		font-weight: 500;
		cursor: pointer;
	}
	/* drop-down list / text element */
	#dropdown {
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 1;
	}
	.selected {
		color: #fff !important;
		background-color: #206095;
	}
	.button {
		color: #fff;
		background-color: #206095;
		background-repeat: no-repeat;
		background-position: center;
		display: inline-block;
		float: right;
	}
	.down {
		background-image: url("../img/chevron-down.svg");
		width: 30px;
	}
	.search {
		background-image: url("../img/search.svg");
		width: 30px;
	}
	.close {
		background-image: url("../img/x-close.svg");
		width: 30px;
	}
	small {
		color: #999;
	}
</style>

<div id="select" class:active={expanded}>
	{#if selected}
	<a id="toggle" class="selected" on:click={toggle}>
		<span class="selection">{selected.name} <small>{selected.group}</small></span>
		<span class="button close" on:click={unSelect}>&nbsp;</span>
	</a>
	{:else}
	<a id="toggle" on:click={toggle}>
		<span>{placeholder ? placeholder : 'Select one'}</span>
		{#if search}
		<span class="button search">&nbsp;</span>
		{:else}
		<span class="button down">&nbsp;</span>
		{/if}
	</a>
	{/if}
	{#if expanded}
	<div id="dropdown" bind:this={el}>
		{#if search}
		<input type="text" placeholder="" bind:value={filter} autocomplete="false" bind:this={input} on:keyup={doKeyup} />
		{/if}
		<ul>
			{#if filtered[0] && filter.length >= 3}
			{#each filtered as option, i}
			<li class:highlight={active == i} on:click="{() => select(option)}" on:mouseover={() => active = i}>
				{option.name} <small>{option.group}</small>
			</li>
			{/each}
			{:else if filtered[0]}
			<li>Type a place name...</li>
			{:else}
			<li>No results</li>
			{/if}
		</ul>
	</div>
	{/if}
</div>